import { useState } from 'react';

interface TimeSlot {
  time: string;
  available: boolean;
}

interface DaySchedule {
  date: number;
  slots: TimeSlot[];
}

export default function BookPage() {
  const [currentMonth] = useState('2025年5月');
  const [selectedDate, setSelectedDate] = useState<number | null>(null);
  const [selectedTime, setSelectedTime] = useState<string | null>(null);
  const [showModal, setShowModal] = useState(false);

  // Mock calendar data
  const daysInMonth: DaySchedule[] = Array.from({ length: 31 }, (_, i) => ({
    date: i + 1,
    slots: i % 3 === 0 ? [
      { time: '10:00', available: true },
      { time: '14:00', available: true },
      { time: '16:00', available: false },
      { time: '18:00', available: true }
    ] : i % 2 === 0 ? [
      { time: '10:00', available: true },
      { time: '14:00', available: false }
    ] : []
  }));

  const handleTimeSlotClick = (date: number, time: string) => {
    setSelectedDate(date);
    setSelectedTime(time);
    setShowModal(true);
  };

  const handleConfirmBooking = () => {
    alert('預約功能開發中，敬請期待！');
    setShowModal(false);
  };

  return (
    <div className="min-h-screen bg-[#F6F1E6] py-20">
      <div className="max-w-7xl mx-auto px-8">
        <h1 className="text-5xl font-bold text-[#0F2E24] text-center mb-16">
          預約課程
        </h1>

        <div className="grid grid-cols-1 lg:grid-cols-12 gap-12">
          {/* Calendar Section */}
          <div className="lg:col-span-8">
            <div className="bg-white rounded-3xl p-8 shadow-sm">
              {/* Calendar Header */}
              <div className="flex items-center justify-between mb-8">
                <button className="w-10 h-10 flex items-center justify-center hover:bg-[#F6F1E6] rounded-full transition-colors cursor-pointer">
                  <i className="ri-arrow-left-s-line text-2xl text-[#0F2E24]"></i>
                </button>
                <h2 className="text-2xl font-bold text-[#0F2E24]">{currentMonth}</h2>
                <button className="w-10 h-10 flex items-center justify-center hover:bg-[#F6F1E6] rounded-full transition-colors cursor-pointer">
                  <i className="ri-arrow-right-s-line text-2xl text-[#0F2E24]"></i>
                </button>
              </div>

              {/* Weekday Headers */}
              <div className="grid grid-cols-7 gap-2 mb-4">
                {['日', '一', '二', '三', '四', '五', '六'].map((day) => (
                  <div key={day} className="text-center text-sm font-medium text-[#8B9A94] py-2">
                    {day}
                  </div>
                ))}
              </div>

              {/* Calendar Grid */}
              <div className="grid grid-cols-7 gap-2">
                {/* Empty cells for alignment */}
                {Array.from({ length: 2 }).map((_, i) => (
                  <div key={`empty-${i}`} className="aspect-square"></div>
                ))}

                {/* Date cells */}
                {daysInMonth.map((day) => (
                  <div
                    key={day.date}
                    className={`aspect-square border rounded-xl p-2 ${
                      day.date === 15 ? 'border-[#0F2E24] border-2' : 'border-[#E5E5E5]'
                    } ${day.slots.length > 0 ? 'bg-white hover:bg-[#F6F1E6]/50 cursor-pointer' : 'bg-[#F6F1E6]/30'} transition-colors`}
                  >
                    <div className="text-sm font-medium text-[#0F2E24] mb-1">{day.date}</div>
                    <div className="space-y-1">
                      {day.slots.slice(0, 3).map((slot, idx) => (
                        <button
                          key={idx}
                          onClick={() => slot.available && handleTimeSlotClick(day.date, slot.time)}
                          disabled={!slot.available}
                          className={`w-full text-xs px-2 py-1 rounded-lg whitespace-nowrap ${
                            slot.available
                              ? 'bg-[#F6F1E6] text-[#0F2E24] hover:bg-[#0F2E24] hover:text-[#F6F1E6] cursor-pointer'
                              : 'bg-gray-200 text-gray-400 cursor-not-allowed'
                          } transition-colors`}
                        >
                          {slot.time}
                        </button>
                      ))}
                      {day.slots.length > 3 && (
                        <div className="text-xs text-[#8B9A94] text-center">+{day.slots.length - 3}</div>
                      )}
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/* Info Sidebar */}
          <div className="lg:col-span-4">
            <div className="bg-white rounded-3xl p-8 shadow-sm sticky top-24">
              <h3 className="text-2xl font-bold text-[#0F2E24] mb-6">預約說明</h3>

              <div className="space-y-4 mb-8">
                <div className="flex items-start gap-3">
                  <i className="ri-checkbox-circle-fill text-[#0F2E24] text-xl mt-1"></i>
                  <span className="text-base text-[#0F2E24]">不需填表單</span>
                </div>
                <div className="flex items-start gap-3">
                  <i className="ri-checkbox-circle-fill text-[#0F2E24] text-xl mt-1"></i>
                  <span className="text-base text-[#0F2E24]">直接選時段</span>
                </div>
                <div className="flex items-start gap-3">
                  <i className="ri-checkbox-circle-fill text-[#0F2E24] text-xl mt-1"></i>
                  <span className="text-base text-[#0F2E24]">預約/取消都在平台完成</span>
                </div>
              </div>

              <div className="border-t border-[#0F2E24]/10 pt-6">
                <p className="text-sm text-[#8B9A94] mb-2">如有疑問，請聯絡</p>
                <a
                  href="mailto:NofficeStudio@gmail.com"
                  className="text-base font-medium text-[#0F2E24] hover:text-[#8B9A94] transition-colors"
                >
                  NofficeStudio@gmail.com
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Booking Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-[#0F2E24]/40 backdrop-blur-sm flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-3xl p-10 max-w-md w-full relative animate-fade-in-up">
            <button
              onClick={() => setShowModal(false)}
              className="absolute top-6 right-6 w-8 h-8 flex items-center justify-center hover:bg-[#F6F1E6] rounded-full transition-colors cursor-pointer"
            >
              <i className="ri-close-line text-2xl text-[#0F2E24]"></i>
            </button>

            <h3 className="text-3xl font-bold text-[#0F2E24] mb-8">確認預約資訊</h3>

            <div className="space-y-5 mb-8">
              <div className="flex items-center gap-4">
                <i className="ri-calendar-line text-[#0F2E24] text-2xl"></i>
                <span className="text-lg text-[#0F2E24]">2025年5月{selectedDate}日 星期四</span>
              </div>
              <div className="flex items-center gap-4">
                <i className="ri-time-line text-[#0F2E24] text-2xl"></i>
                <span className="text-lg text-[#0F2E24]">{selectedTime} - {selectedTime && `${parseInt(selectedTime.split(':')[0]) + 1}:00`}</span>
              </div>
              <div className="flex items-center gap-4">
                <i className="ri-map-pin-line text-[#0F2E24] text-2xl"></i>
                <span className="text-lg text-[#0F2E24]">台北市大安區（到府服務）</span>
              </div>
            </div>

            <div className="bg-yellow-50 rounded-xl p-4 mb-4">
              <div className="flex items-start gap-3">
                <i className="ri-alert-line text-yellow-600 text-xl mt-0.5"></i>
                <span className="text-base text-[#0F2E24]">請於預約後 1 小時內完成轉帳</span>
              </div>
            </div>

            <p className="text-sm text-[#8B9A94] mb-8">
              取消政策：課程前 24 小時可免費取消
            </p>

            <button
              onClick={handleConfirmBooking}
              className="w-full bg-[#0F2E24] text-[#F6F1E6] py-4 rounded-full text-lg font-medium hover:bg-[#1A4435] transition-colors mb-3 whitespace-nowrap cursor-pointer"
            >
              確認預約
            </button>
            <button
              onClick={() => setShowModal(false)}
              className="w-full text-[#8B9A94] py-2 text-base hover:text-[#0F2E24] transition-colors cursor-pointer"
            >
              返回選擇
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
